
import Vue from 'vue'
import Router from 'vue-router'
// 
import Home from '../pages/home/home.vue'
import BookAudio from '../pages/bookAudio/bookAudio.vue'
import Radio from '../pages/radio/radio.vue'
import Group from '../pages/group/group.vue'
import Mine from '../pages/mine/mine.vue'
// 

import baMovie from '../pages/bookAudio/bookaudio-sec/ba-movie.vue'
import baRead from '../pages/bookAudio/bookaudio-sec/ba-read.vue'
import baTv from '../pages/bookAudio/bookaudio-sec/ba-tv.vue'
import baCity from '../pages/bookAudio/bookaudio-sec/ba-city.vue'
import baMusic from '../pages/bookAudio/bookaudio-sec/ba-music.vue'

Vue.use(Router)

var children=[{
  path:'/bookAudio/baMovie',
  component:baMovie
},{
  path:'/bookAudio/baRead',
  component:baRead
},{
  path:'/bookAudio/baTv',
  component:baTv
},{
  path:'/bookAudio/baCity',
  component:baCity
},{
  path:'/bookAudio/baMusic',
  component:baMusic
}]

export default new Router({
  routes: [
    {
      path:'/',
      component:Home
    },{
      path:'/home',
      component:Home
    },{
      path:'/bookAudio',
      component:BookAudio,children
    },{
      path:'/radio',
      component:Radio
    },{
      path:'/group',
      component:Group
    },{
      path:'/mine',
      component:Mine
    }
  ]
})



