<template>

    <div class="mineItem"  >
      <span ><slot name="mine-item-img"></slot></span>
      <div  class="item-text" v-text="mineText"></div>
    </div>

</template>
<script>
  export default {
    props: ['mineText']

  }
</script>

<style>

  .mineItem {
    width: 25%;
    height: 100%;
    float: left;
    box-sizing: border-box;
    text-align: center;
  }

  .item-text {
    font-size: 1.3rem;
    color: #666;
    text-align: center;
  }
  .mineItem span {
    display: inline-block;
    margin: 1px auto 0 auto;
    width: 3rem;
    height: 3rem;
  }
  .mineItem img{
    width: 100%;
  }
  .mineItem .active{
    color:#44bf55;
  }

</style>



