<template>
  <div>
    <div class="top ">
      <div class="title" v-text="text"></div>
      <slot name="input"></slot>
      <div class="one">
        <slot name="imgone"></slot>
      </div>
      <div class="two">
        <slot name="imgtwo"></slot>
      </div>
    </div>
    <slot name="kong"></slot>
  </div>

</template>

<script>

  export default {
    props: ['text'],

  }
</script>

<style>
  .row{
    margin:0;
  }

  .top {
    width: 100%;
    height: 40px;
    background-color: #f7f7f7;
    position: relative;
    border-bottom: 1px solid #ddd;
    position: fixed;
    top: 0;
    z-index: 99;
  }
  .title {
    width: 100%;
    font-size: 1.6rem;
    text-align: center;
    line-height: 40px;
  }

  .one, .two {
    position: absolute;
    top: 7px;
    width: 2.5rem;
    height: 2.5rem;
  }

  .one {
    right: 50px;
  }

  .two {
    right: 10px;
  }
.kong{
  width: 100%;
  height: 40px;
}
  .book-kong{
    width: 100%;
    height: 90px;
  }
</style>



