<template>
  <div>
    <topbar>
      <div class="inp-box" slot="input">
        <div class="inp">
          <div class="search"></div>
          <input type="text" placeholder="   "/>
          <div class="sao"></div>
        </div>
      </div>
      <img slot="imgtwo" src="../../assets/ic_chat_white.png" alt=""/>
      <div class="kong" slot="kong"></div>
    </topbar>
    <div class="slider">
      <div id="carousel-example-generic" class="carousel slide" data-ride="carousel">
        <!-- Indicators -->
        <ol class="carousel-indicators">
          <li data-target="#carousel-example-generic" data-slide-to="0" class="active"></li>
          <li data-target="#carousel-example-generic" data-slide-to="1"></li>
          <li data-target="#carousel-example-generic" data-slide-to="2"></li>
        </ol>

        <!-- Wrapper for slides -->
        <div class="carousel-inner" role="listbox">
          <div class="item active">
            <img src="../../assets/banner/01.jpg" alt="">
          </div>
          <div class="item">
            <img src="../../assets/banner/02.jpg" alt="">
          </div>
          <div class="item">
            <img src="../../assets/banner/03.jpg" alt="">
          </div>
        </div>
      </div>
    </div>
    <h2 class="home-hot"></h2>
    <homeContent itemTitle="14"
                  itemText="."
                  itemAuthor="">
      <img src="../../assets/media.jpg" alt="" slot="home-hot-item-img">
    </homeContent>
    <homeContent itemTitle=""
                 itemText="..."
                 itemAuthor="">
      <img src="../../assets/media.jpg" alt="" slot="home-hot-item-img">
    </homeContent>
    <homeContent itemTitle=""
                 itemText="..."
                 itemAuthor="">
      <img src="../../assets/media.jpg" alt="" slot="home-hot-item-img">
    </homeContent>
  </div>
</template>
<script>
  $('.carousel').carousel({
    interval: 2000
  })
  import topbar from '../../components/topbar.vue'
  import homeContent from './homeContent.vue'
  export default {

    components: {
      topbar,homeContent
    }
  }
</script>
<style>
  .home-hot{
    color:#f28743;
    font-size: 20px;
    line-height: 24px;
    border-left:5px solid #f28743;
    text-indent: 1em;
  }
  .home-hot-item{
    border-bottom:1px solid #eee;
  }
  .home-hot-item-img img{
    width: 100%;
  }
  .home-hot-item-line{
    width: 20px;
    margin:10px 0;
    border-top:1px solid #ccc;
  }
  .home-hot-item-text{
    color:#999;
  }
  .home-hot-item-author{
    color: #bbb;
  }
</style>



