        <h4 v-text="itemTitle"></h4>
        <p class="home-hot-item-text" v-text="itemText"></p>
        <p class="home-hot-item-line"></p>
        <p class="home-hot-item-author"  >: <span v-text="itemAuthor"></span></p>
