<template>
  <div>
    <topbar text="">
      <img slot="imgone" src="../../assets/ic_actionbar_search_icon.png" alt=""/>
      <img slot="imgtwo" src="../../assets/ic_create_group_chat_blue.png" alt=""/>
      <div class="book-kong" slot="kong"></div>
    </topbar>
    <div class="book-sec">
      <ul>
        <router-link v-for="item in arr" key="item" :to="item.link">
          <li :class="{movieActive:item.link===selected}" @click="fn(item.link)" v-text="item.name"></li>
        </router-link>
      </ul>
      <router-view></router-view>
    </div>
  </div>
</template>
<script>

  import topbar from '../../components/topbar.vue'

  export default {
    data() {
      return {
        boole: false,
        selected: '',
        arr: [{
          name: '',
          link: '/bookAudio/baMovie'
        }, {
          name: '',
          link: '/bookAudio/baRead'
        }, {
          name: '',
          link: '/bookAudio/baTv'
        }, {
          name: '',
          link: '/bookAudio/baCity'
        }, {
          name: '',
          link: '/bookAudio/baMusic'
        },]
      }

    },
    components: {
      topbar
    },
//    beforeCreate:function(){
//      this.$router.push('/bookAudio/baMovie')
//    },
    created:function(){
//      hash#
      var hash=window.location.hash.substr(1);
      if(hash=='/bookAudio'){
        this.selected='/bookAudio/baMovie'
        return;
      }
      this.selected=hash;
//      console.log(hash)
    },
    methods: {
      fn: function (link) {
        this.selected = link
      }
    }
  }
</script>
<style>
  .book-sec > ul {
    margin: 0;
    height: 52px;
    width: 100%;
    background-color: #fff;
    border-bottom: 1px solid #ccc;
    position: fixed;
    top:40px;
  }

  .book-sec > ul li {
    line-height: 30px;
    height: 50px;
    float: left;
    width: 20%;
    padding: 10px 0;
    text-align: center;
    border-bottom: 2px solid transparent;
  }

  .book-sec {
    background-color: #f7f7f7;
    vertical-align: top;
  }

  .movie-item {
    background-color: #fff;
    padding: 10px;
    margin-top: 10px;
    overflow: hidden;
  }

  .movie-item > h3, .bottom-title h3 {
    font-size: 16px;
  }

  .bottom-title h3 {
    margin: 0;
  }

  .bottom-title {
    padding: 10px;
    background-color: #fff;
  }

  .movie-item > h3 > span:last-of-type {
    font-size: 13px;
    color: #44bf55;
  }

  .movie-item > .movie-item-hot {
    display: flex;
    width: 3000px;
  }

  .movie-item > .movie-item-hot .movie-item-hot-item {
    width: 90px;
    padding: 5px;
  }

  .movie-item > .movie-item-hot .movie-item-hot-item > p {
    font-size: 12px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    margin: 5px 0 5px 0;
  }

  .movie-item > .movie-item-hot .movie-item-hot-item > img {
    /*width: 100%;*/
    height: 120px;
  }

  .movie-star {
    display: flex;
    align-items: center;
  }

  .movie-star span {
    width: 9px;
    height: 9px;
    font-size: 10px;
    display: flex;
    align-items: center;
  }

  .movie-star span img {
    width: 100%;
    vertical-align: middle;
  }

  .movie-banner {
    margin-top: 10px;
  }

  .movie-banner img {
    width: 100%;
  }

  .book-sec > ul .movieActive {
    color: #44bf55;
    border-bottom: 2px solid #44bf55;
  }
</style>



