<template>
    <div class="bottom">
      <router-link to="/home">
        <bottombar text="" mark="home" :selected="selected" @change="getval">
          <img src="../assets/ic_tab_home_normal.png" alt="" slot='item-img'>
          <img src="../assets/ic_tab_home_active.png" alt="" slot='active-img'>
        </bottombar>
      </router-link>

      <router-link to="/bookAudio/baMovie">
        <bottombar text="" mark="/bookAudio/baMovie" :selected="selected"
                   @change="getval">
          <img src="../assets/ic_tab_status_normal.png" alt="" slot='item-img'>
          <img src="../assets/ic_tab_status_active.png" alt="" slot='active-img'>
        </bottombar>
      </router-link>

      <router-link to="/radio">
        <bottombar text="" mark="radio" :selected="selected" @change="getval">
          <img src="../assets/ic_tab_subject_normal.png" alt="" slot='item-img'>
          <img src="../assets/ic_tab_subject_active.png" alt="" slot='active-img'>
        </bottombar>
      </router-link>

      <router-link to="/group">
        <bottombar text="" mark="group" :selected="selected" @change="getval">
          <img src="../assets/ic_tab_group_normal.png" alt="" slot='item-img'>
          <img src="../assets/ic_tab_group_active.png" alt="" slot='active-img'>
        </bottombar>
      </router-link>

      <router-link to="/mine">
        <bottombar text="" mark="mine" :selected="selected" @change="getval">
          <img src="../assets/ic_tab_profile_female_normal.png" alt="" slot='item-img'>
          <img src="../assets/ic_tab_profile_female_active.png" alt="" slot='active-img'>
        </bottombar>
      </router-link>
    </div>


</template>

<script>

  import bottombar from './bottombar.vue'
  export default {
    components: {
      bottombar
    },
    data:function(){
      return{
        selected:''
      }
    },
    beforeCreate:function(){
//      this.$router.push('/home')
      this.selected='#/home'
    },
    created:function(){
      var hash=window.location.hash.split('/')[1];
      if(hash=='#/'){
        this.selected='#/home'
      }else if(hash=='bookAudio'){
        this.selected='/bookAudio/baMovie'
      }else{
        this.selected=hash
      }
    },
    methods:{
      getval:function(val){
        this.selected=val;
       console.log( this.selected)
      }
    }
  }

</script>

<style>

  html {
    font-size: 10px;
  }

  * {
    margin: 0;
    padding: 0;
  }

  ul {
    list-style: none;
  }

  .one img, .two img {
    width: 100%;
  }

  /**/
  .inp-box {
    width: 100%;
    height: 40px;
    background-color: #42c055;
  }

  .inp {
    position: relative;
    width: 85%;
    height: 30px;
    padding: 5px 0 0 5px;
  }

  .inp input {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #fff;
    text-indent: 2em;
    border-radius: 5px;
    border: none;
    outline: none;
    font-size: 1.4rem;
  }

  .inp .search, .inp .sao {
    position: absolute;
    width: 20px;
    height: 20px;
    top: 9px;
    z-index: 2;
  }

  .inp .search {
    left: 10px;
    background: url("../assets/ic_search_gray.png") no-repeat;
    background-size: contain;
  }

  .inp .sao {
    right: 4px;
    background: url("../assets/ic_scan_gray.png") no-repeat;
    background-size: contain;
  }

  /**/
  .bottom {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 70px;
    background-color: #eee;
    border-top: 1px solid #ddd;
    z-index: 1;
  }


</style>



